




























import Vue from 'vue'
import date from 'date-and-time'
import ru from 'date-and-time/locale/ru'
date.locale(ru);

export default Vue.extend({
  name: 'NewsItem',
  components: {
    
  },
  props: {
    href: {
      type: String,
      required: true,
    },
    title: {
      type: String,
      required: true,
    },
    img: String,
    datetime: {
      required: true,
    },
    description: String,
    tag: String,
    source: String
  },
  data () {
    return {
      date: date,
    }
  },
  methods: {

  }
})
