























import Vue from 'vue';
import NewsItem from '@/components/NewsItem.vue'; // @ is an alias to /src
import axios from 'axios'
import qs from 'qs';

export default Vue.extend({
  name: 'HomeView',
  components: {
    NewsItem,
  },
  data () {
    return {
      pageCount: 0,
      baseUrl: '//localhost:1337/api',
      page: 1,
      limit: 6,
      news: []
    }
  },
  methods: {
    clickPageH (n) {
      this.page = n
      this.getNews()
    },
    getNews () {
      axios.get(this.baseUrl+'/news', {
        params: {
          //locale: 'ru-RU',
          sort: [ 'createdAt:desc' ],
          pagination: {
            start: (this.page-1)*this.limit,
            limit: this.limit
          },
          fields: ['name', 'slug', 'description', 'createdAt'],
          populate: {
            preview_img: {
              fields: ['name', 'url'],
            },
            source: {
               fields: ['name'],
            },
            tags: {
               fields: ['name'],
            }
          }
        },
        paramsSerializer: p => {
          return qs.stringify(p)
        }
      })
        .then(response => {
          this.news = response.data.data;
          this.pageCount = Math.ceil(response.data.meta.pagination.total / this.limit);
        })
        .catch(response => {
          console.log(response);
        })
    }
  },
  created () {
    this.getNews()
  }
});
